.page-container {
      padding-top: 37px;
      padding-bottom: 43px;
      height: 100%;
      display: flex;
      justify-content: center;
  }
.top{
  position: fixed;
  left: 0px;
  top: 0px;
  z-index: 1000;
  width: 100%;
  background-color: rgb(255, 133, 42);
}
.top .header-bd{
  width: 100%;
  padding: 6px 9px 6px 42px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-size: 27px 20px;
  background-position: 9px center;
}
.top .header-bd .placeholder {
  display: block;
  font-size: 0px;
  background-color: rgb(255, 78, 34);
  text-align: center;
  border-radius: 4px;
}

.top .header-bd .placeholder .text {
  height: 25px;
  line-height: 25px;
  font-size: 13px;
  display: inline-block;
  color: rgb(240, 224, 220);
}
.conunt {
  position: relative;
  width: 100vw;
  height: 100%;
}
.content {
  min-height: 100%;
  width: 100vw;
  background-color: rgb(244, 244, 244);
}
.content .lunbotu {
  height: 175px;
  width: 100vw;
}
.shang-box{
    height: 200px;
    position: relative;
    width: 170px;
    box-sizing: border-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    flex-direction: column;
    align-content: flex-start;
    flex-shrink: 0;
    background: #fff;
    margin-top: 9px;
    border-radius: 6px;
    overflow: hidden;
    margin-left: 4.5px;
    margin-right: 4.5px;
}
.box{
  display: -webkit-flex;
  display: flex;
  position: relative;
  box-sizing: border-box;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row;
  place-content: flex-start space-between;
  flex-shrink: 0;
  overflow: hidden;
  -webkit-box-pack: justify;
  -webkit-box-lines: multiple;
  -webkit-flex-wrap: wrap;
  flex-wrap: wrap;
  padding: 0 7.5px;
  background: #f2f2f2;
  padding-bottom: 12px;
}
.fanhui{
  border: none;
  border-radius: 30px;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 10px;
  left: 10px;
}
.top1{
  float: right;
}